.header {
  width: 100%;
  padding: 34px 0;
  background-color: var(--color-white);
  position: relative;
  z-index: 11;

  @include media(sm) {
    padding: 21px 0;
  }
  
  .container {
    max-width: 1920px;
    width: 100%;
    padding-left: 130px;
    padding-right: 100px;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;

    @include media(lg) {
      padding: 0 20px;
    }
  }

  &__logo {

    img {
      @include media(sm) {
        height: 20px;
      }
    }
  }

  &__search {
    margin: 0 70px 0 auto;
    cursor: pointer;

    &:hover {
      opacity: .7;
    }
    svg {
      width: 20px;
      height: 20px;
    }

    @include media(sm) {
      order: -1;
      margin: 0;
    }
  }

  &__lang {
    margin-right: 70px;
    cursor: pointer;

    &:hover {
      opacity: .7;
    }

    @include media(sm) {
      display: none;
    }
  }

  &__burger {
    cursor: pointer;
    svg {
      width: 33px;
      height: 23px;
    }
  }
}