.events {
  padding-top: 302px;
  padding-bottom: 150px;
  padding-left: 246px;

  @media(max-width: 1600px) {
    padding-left: 160px;
  }

  @include media(lg) {
    padding-left: 0;
  }

  @include media(sm) {
    padding: 50px 0;
  }

  .container {
    max-width: 1472px;
    padding: 0 20px;
  }

  &__title {
    display: inline-block;
    margin-bottom: 150px;
    position: relative;
    padding-bottom: 10px; 
    width: auto;

    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      height: 16px;
      width: 100%;
      background: url('../images/ico-wave-ocean.svg') left center / contain repeat-x;
    }

    @include media(sm) {
      margin-bottom: 55px;
      padding-bottom: 10px;
      &::before {
        height: 5px;
        
      }
    }
  }

  &-list {
    margin-bottom: 150px;

    @include media(sm) {
      margin-bottom: 50px;
    }
  }

  &__item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    gap: 40px;

    + .events__item {
      margin-top: 100px;
    }

    @include media(xs) {
      flex-direction: column;
      gap: 0;

      + .events__item {
        margin-top: 50px;
      }

      &--right {
        flex-direction: column-reverse;
      }
    }

   

    &-img {
      position: relative;
      max-width: 640px;

      &--yellow {

        &::before {
          position: absolute;
          bottom: -40px;
          right: -40px;
          content: '';
          width: 100%;
          height: 193px;
          background: url('../images/event-wave-1.svg') left top / cover no-repeat;
          z-index: -1;

          @include media(md) {
            height: 100px;
            bottom: -20px;
            right: -20px;
          }
        }
      }

      &--green {
        
        &::before {
          position: absolute;
          bottom: -40px;
          left: -40px;
          content: '';
          width: 100%;
          height: 193px;
          background: url('../images/event-wave-2.svg') left top / cover no-repeat;
          z-index: -1;

          @include media(md) {
            height: 100px;
            bottom: -20px;
            left: -20px;
          }

          @include media(xs) {
            left: auto;
            right: -20px;
          }
        }
      }

      @include media(lg) {
        max-width: 400px;
      } 

      @include media(xs) {
        max-width: calc(100% - 20px);
        margin: 0 auto 50px 0;
      }
    }

    &-info {
      max-width: 549px;
      flex-shrink: 0;

      @include media(xs) {
        align-self: flex-start;
        padding-left: 40px;
      }
    }

    &-date {
      font-size: 24px;
      line-height: 1.25;
      margin-bottom: 55px;
      color: var(--color-green);

      @include media(sm)  {
        margin-bottom: 10px;
        font-size: 13px;
      }
    }

    &-title {
      font-size: 64px; 
      line-height: 1.3;
      margin-bottom: 58px;

      @include media(lg) {
        font-size: 54px;
      }

      @include media(md) {
        font-size: 40px;
      }

      @include media(sm) {
        font-size: 32px;
        margin-bottom: 30px;
      }

      @include media(xs) { 
        font-size: 18px;
        
      }
    }

    &-text {
      max-width: 360px;
      font-size: 22px;
      line-height: 1.2;

      @include media(sm) {
        max-width: 280px;
        font-size: 18px;
      }

      @include media(xs) {
        max-width: 225px;
        font-size: 15px;
      }
    }
  }

  &__more {
    display: block;
    max-width: 198px;
    margin: 0 auto;
  }
}


.title-wrap {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  span {
    width: auto;
    display: inline-block;
    padding-bottom: 6px;
    position: relative;

    &::before {
      content: '';
      position: absolute;
      bottom: 0;
      right: 0;
      left: 0;
      width: 100%;
      height: 11px;
      background: url('../images/ico-wave.svg') left center / contain repeat-x;
    }

    @include media(sm) {
      padding-bottom: 0px;
      
      &::before {
        height: 4px;
      }
    }
  }
}