.hero {

  &-slider {
    overflow: hidden;
    position: relative;

    .swiper-pagination {
      display: inline-flex;
      width: auto;
      bottom: 100px;
      right: 100px;
      left: auto;

      &-bullet {
        width: 15px;
        height: 2px;
        background: var(--color-white);
        opacity: .5;

        &-active {
          background: var(--color-white);
          opacity: 1;
        }
      }

      @include media(sm) {
        bottom: 55px;
        right: 16px;
      }
    }
  }

  &__slide {
    position: relative;
    min-height: 880px;
    padding-bottom: 47px;
    padding-left: 246px;
    display: flex;
    align-items: flex-end;

    @media(max-width: 1600px) {
      padding-left: 160px;
    }

    @include media(lg) {
      padding-left: 50px;
    }

    @include media(sm) {
      padding-left: 15px;
      min-height: 600px;
    }

    &-img {
      position: absolute;
      top: 0;
      right: 0;
      left: 0;
      bottom: 0;
      z-index: 1;

      img {
        display: block;
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: left bottom;
      }

      @include media(sm) {
        img {
          object-position: top center;
        }
      }

      &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        top: 0;
        right: 0;
        left: 0;
        bottom: 0;
        background: linear-gradient(360deg, rgba(17, 17, 17, 0.75) 0%, rgba(17, 17, 17, 0) 74.48%);
      }
    }

    &-info {
      position: relative;
      z-index: 2;
      color: var(--color-white);
    }

    &-type {
      font-size: 12px;
      margin-bottom: 40px;
      text-transform: uppercase; 
      letter-spacing: 3px;

      @include media(sm) {
        font-size: 10px;
        margin-bottom: 24px;
      }
    }

    &-title {
      max-width: 592px;
      font-size: 64px;
      line-height: 1;
      margin-bottom: 26px;

      @include media(sm) {
        max-width: 278px;
        font-size: 35px;
        margin-bottom: 24px;
      }
    }

    &-desc {
      max-width: 954px;
      font-size: 18px;
      line-height: 1.3;
      margin-bottom: 50px;

      @include media(sm) {
        display: none;
      }
    }


    &-wave {
      width: 100%;
      background: url('../images/ico-wave-white.svg') left center / contain repeat-x;
      height: 7px;
      margin-bottom: 26px;

      @include media(md) {
        display: none;
      }
    }

    &-btn {
      position: relative;
      display: inline-block;
      width: auto;
      padding: 0 18px 0 59px;
      border: 2px solid var(--color-white);
      border-radius: 50px;
      height: 48px;
      text-transform: uppercase;
      font-size: 15px;
      line-height: 48px;
      cursor: pointer;

      span {
        position: relative;
        z-index: 2;
      }

      &::before {
        position: absolute;
        content: '';
        height: 48px;
        width: 48px;
        border-radius: 50px;
        border: 2px solid var(--color-white);
        top: -2px;
        left: -1px;
        bottom: 1px;
        transition: .2s;
      }

      &:hover {

        color: var(--color-dark);

        &::before {
          width: 100%;
          background-color: var(--color-white);
          transition: .5s;
          border-radius: 50px;
          left: 0;
        }
      }
    }
  }
}