.btn {
  position: relative;
  display: inline-block;
  width: auto;
  padding: 0 18px 0 59px;
  border: 2px solid var(--color-white);
  border-radius: 50px;
  height: 48px;
  text-transform: uppercase;
  font-size: 15px;
  line-height: 48px;
  cursor: pointer;

  span {
    position: relative;
    z-index: 2;
  }

  &::before {
    position: absolute;
    content: '';
    height: 48px;
    width: 48px;
    border-radius: 50px;
    border: 2px solid var(--color-white);
    top: -2px;
    left: -1px;
    bottom: 1px;
    transition: .2s;
  }

  &:hover {

    color: var(--color-dark);

    &::before {
      width: 100%;
      background-color: var(--color-white);
      transition: .5s;
      border-radius: 50px;
      left: 0;
    }
  }

  &--invert {
    border-color: var(--color-dark);
    color: var(--color-dark);

    &::before {
      border-color: var(--color-dark);
    }

    &:hover {
      color: var(--color-white);

      &::before {
        background-color: var(--color-dark);
      }
    }
  }
}