:root {
  
  // Global
  --font-family: 'HelveticaNeueCyr', sans-serif;
  --font-size: 16px;
  --line-height: 1.5;
  --border-radius: 8px;
  --box-shadow: rgb(33 35 38 / 10%) 0 10px 10px -10px;
  --transition: 0.4s ease-in-out;

  // Colors
  --color-black: #111111;
  --color-white: #fff;
  --color-dark: #303030;
  --color-green: #82BF8C;
  --color-ocean: #12919C;
  --color-border: #EEEEEE;

  // Z-index
  --z-index-header: 100;
  --z-index-menu: 200;
  --z-index-dropdown: 300;
  --z-index-modal: 1000;
}
