.stocks {
  border-top: 2px solid var(--color-black);
  padding: 150px 0;

  @include media(md) {
    padding: 100px 0;
  }

  @include media(sm) {
    padding: 50px 0;
  }

  .container {
    max-width: 1920px;
    padding: 0 0 0 246px;
    margin: 0 auto;

    @media (max-width: 1600px) {
      padding: 0 20px 0 160px;
    }
  
    @include media(sm) {
      padding: 50px 16px;
    }
  }



  &__title {
    display: inline-block;
    margin-bottom: 140px;
    position: relative;
    padding-bottom: 10px; 
    width: auto;

    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      height: 16px;
      width: 100%;
      background: url('../images/ico-wave-ocean.svg') left center / contain repeat-x;
    }

    @include media(sm) {
      margin-bottom: 50px;
      padding-bottom: 10px;
      &::before {
        height: 5px;
        
      }
    }
  }

  &-list {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    gap: 10px;

    @include media(lg) {
      grid-template-columns: 1fr 1fr;
    }

    @include media(md) {
      display: flex;
      flex-direction: column;
      gap: 8px;
    }
  }

  &__item {
    min-height: 810px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: flex-end;
    overflow: hidden;

    &:hover {
      .stocks__item-img {
        transform: scale(1.05);
        transition: transform .5s;
      }
    }

    @media(max-width: 1600px) {
      min-height: 600px;
    }

    @include media(sm) {
      min-height: 500px;
    }
    
    @include media(xs) {
      min-height: 440px;
    }

    &--xl {
      grid-column: span 2;
      grid-row: span 2;

      .stocks__item-info {
        padding-left: 159px;
      }

      @include media(lg) {
        grid-row: 1 / 2;
        .stocks__item-info {
          padding-left: 40px;
        }
      }

      @include media(sm) {
        order: -1;
      }

    }

    &-info {
      width: 100%;
      padding: 48px 50px 48px 50px;

      color: var(--color-white);

      @include media(lg) {
        padding: 44px 22px 27px 40px;
      }
    }

    &-title {
      font-size: 36px;
      line-height: 1.3;
      margin-bottom: 46px;

      @include media(lg) {
        font-size: 24px;
        margin-bottom: 24px;
      }

      @include media(sm) {
        font-size: 18px;
        margin-bottom: 30px;
      }
    }

    &-dates {
      font-size: 24px;

      @include media(sm) {
        font-size: 13px;
      }
    }


    &-img {
      position: absolute;
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      z-index: -1;
      transition: transform .2s;

      img {
        width: 100%;
        height: 100%;
        object-fit: cover;
        object-position: top center;
        display: block;
      }
    }

    &--green {

      .stocks__item-info {
        background: linear-gradient(0deg, #82BF8C 0%, rgba(130, 191, 140, 0) 100%);
      }
    }

    &--orange {

      .stocks__item-info {
        background: linear-gradient(0deg, #F37128 0%, rgba(243, 113, 40, 0) 100%);
      }
    }

    &--secondary {

      .stocks__item-info {
        color: var(--color-black);
        background: linear-gradient(0deg, #FFFFFF 0%, rgba(243, 113, 40, 0) 100%);
      }

      .stocks__item-dates {
        color: var(--color-ocean);
      }

      .stocks__item-img {
        position: static;

        img {
          height: auto;
        }
      }
    }
  }
  
}