.gallery {
  padding: 150px 0 100px;
  position: relative;

  @include media(md) {
    padding: 100px 0;
  }

  @include media(sm) {
    padding: 50px 0;
  }

  &-divider {
    height: 8px;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    background: url('../images/ico-wave.svg') top left / contain repeat-x;

    @include media(sm) {
      background: none;
      border-top: 2px solid var(--color-black);
    }
  }

  .container {
    padding-left: 246px;

    @media(max-width: 1600px) {
      padding: 0 20px 0 160px;
    }

    @include media(lg) {
      padding: 0 20px;
    }

    @include media(sm) {
      padding: 0 16px;
    }
  }

  &__title {
    display: inline-block;
    margin-bottom: 150px;
    position: relative;
    padding-bottom: 10px; 
    width: auto;

    &::before {
      position: absolute;
      content: '';
      bottom: 0;
      height: 16px;
      width: 100%;
      background: url('../images/ico-wave-orange.svg') left center / contain repeat-x;
    }

    @include media(sm) {
      margin-bottom: 55px;
      padding-bottom: 10px;
      &::before {
        height: 5px;
        
      }
    }
  }

  &__hashtag {
    font-size: 144px;
    line-height: 1;
    letter-spacing: 40px;
    margin-bottom: 150px;
    font-weight: 300;
    position: relative;

    &-circle {
      width: 440px;
      height: 440px;
      background: #F37128;
      border-radius: 50%;
      position: absolute;
      mix-blend-mode: screen;
      top: -200px;
      right: 30%;
      
    }

    @media(max-width: 1919px) {
      letter-spacing: 50px;
      font-size: 100px;

      &-circle {
        width: 360px;
        height: 360px;
        right: 30%;
        top: -180px;
      }
    }
    @media(max-width: 1440px) {
      font-size: 56px;
    }

    @include media(md) {
      font-size: 56px;
      letter-spacing: 20px;
      margin-bottom: 60px;

      &-circle {
        display: none;
      }
    }

    @include media(sm) {
      font-size: 32px;
      letter-spacing: 10px;
      margin-bottom: 30px;
    }
  }

  &-items {
    overflow: hidden;
    cursor: grab;
  }

  &__img {

    img {
      width: 100%;
      display: block;
    }
  }
}