.menu {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  height: 100vh;
  z-index: 10;
  background-color: var(--color-white);
  padding: 120px 35px 80px;
  width: 90px;
  transition: width .5s;
  box-shadow: 5px 0px 8px 0px rgba(34, 60, 80, 0.2);

  @include media(lg) {
    display: none;
  }

  &-left {
    height: 100%;
  }

  &__list {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    height: 100%;
  }


  &:hover {
    width: 362px;
    transition: .3s;
    
    .menu__item-text,
    .menu__schedule-list {
      visibility: visible;
      opacity: 1;
    }

    .menu__schedule-icon {
      width: 90px;
      height: 90px;
      transition: .3s;
      background: rgba(130, 191, 140, 0.25);
      border-radius: 50%;
      position: relative;

      &::before {
        visibility: visible;
        opacity: 1;
        transition: .3s;
      }
    }
  }

  &__item {
    > a {
      display: flex;
      align-items: center;

      &:hover {

        color: var(--color-ocean);

        .menu__item-icon {
          .icon {
              fill: var(--color-ocean);
          }
        }

        .menu__item-text::before {
          width: 100%;
          transition: width .4s;
        }
      }
    }
    margin-bottom: 24px;
    &:last-of-type {
      margin-bottom: 0;
    }

    &-icon {
      margin-right: 22px;

      .icon {
        width: 24px;
        height: 24px;
        
        fill: var(--color-black);
      }
    }

    &-text {
      display: block;
      position: relative;
      padding-bottom: 7px;
      overflow: hidden;
      opacity: 0;
      visibility: hidden;
      white-space: nowrap;

      &::before {
        position: absolute;
        width: 0;
        content: '';
        bottom: 0;
        right: 0;
        left: 0;
        height: 5px;
        background: url('../images/ico-wave-ocean.svg') left center / contain repeat-x;
        transition: width .4s;
      }
    }

    
  }


  &__schedule {
    margin: auto 0 0 0;
    display: flex;
    height: auto;
    
    > a {
      display: flex;
      align-items: center;
    }

    &-icon {
      display: inline-flex;
      width: 30px;
      height: 30px;
      margin-right: 22px;
      transition: .5s;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      position: relative;

      &::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        top: 0;
        left: 0;
        background: url('../images/ico-time-part.svg') top right / 45px no-repeat;
        opacity: 0;
        visibility: hidden;
      }

      .icon {
        display: block;
        width: 28px;
        height: 28px;
        fill: var(--color-ocean);
      }
    }

    &-list {
      display: flex;
      flex-direction: column;
      opacity: 0;
      visibility: hidden;
      white-space: nowrap;
    }

    &-item {
      font-size: 14px;
      line-height: 1.2;
      span {
        display: block;
      }

      &:first-of-type {
        span {
          color: var(--color-ocean)
        }
      }

      &:last-of-type {
        span {
          color: #82BF8C;
        }
      }
    }
  }

}