.footer {
  position: relative;
  &__logo {
    position: absolute;
    top: -10px;
    padding: 0 37px;
    background-color: var(--color-white);
    left: 50%;
    transform: translateX(-50%);

    @include media(md) {
      display: none;
    }
  }

  &-top {
    padding: 99px 190px 85px 250px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    border-top: 2px solid var(--color-border);
    gap: 24px;

    @media(max-width: 1600px) {
      padding-left: 160px;
      padding-right: 100px;
    }

    @include media(lg) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @include media(md) {
      border-color: var(--color-black);
    }

    @include media(sm) {
      flex-direction: column;
      padding-top: 50px;
      padding-bottom: 35px;
      gap: 50px;
    }
  }

  &-middle {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr auto;
    padding: 70px 205px 35px 250px;
    border-top: 2px solid var(--color-border);
    gap: 20px;

    @media(max-width: 1600px) {
      padding-left: 160px;
      padding-right: 100px;
    }

    @include media(lg) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @include media(md) {
      grid-template-columns: repeat(2 , 1fr);
      gap: 40px;
    }

    @include media(sm) {
      grid-template-columns: 1fr;
      gap: 56px;
      padding-top: 35px;
      border-top: none;
    }
  }

  &-bottom {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 35px 100px 120px 250px ;

    color: #999999;

    @media(max-width: 1600px) {
      padding-left: 160px;
      padding-right: 100px;
    }

    @include media(lg) {
      padding-left: 20px;
      padding-right: 20px;
    }

    @include media(sm) {
      flex-direction: column;
      align-items: center;
      gap: 16px;
      text-align: center;
      padding-bottom: 47px;
    }
  }

  &__col {
    display: flex;
    flex-direction: column;

    @include media(sm) {
      display: none;

      &-mob {
        max-width: 400px;
        margin: 0 auto;
        display: grid;
        grid-template-columns: 1fr 1fr;
        gap: 22px 30px;
      }
    }
  }

  &__link {
    font-size: 18px;
    line-height: 1.5;

    &-icon {
      display: flex;
      align-items: center;

      @include media(sm) {
        font-size: 14px;
      }

      .icon {
        width: 24px;
        height: 24px;
        display: block;
        margin-right: 30px;

        @include media(sm) {
          margin-right: 24px;
        }
      }
    }

    + .footer__link {
      margin-top: 18px;

      @include media(sm) {
        margin-top: 0;
      }
    }
  }

  &__contacts {

    @include media(sm) {
      text-align: center;
    }
    
    &-title {
      font-size: 18px;
      line-height: 1.5;
      margin-bottom: 6px;
    }

    &-tel {
      font-size: 36px;
      line-height: 1;

      @include media(sm) {
        font-size: 24px;
      }
    }
  }

  &__socials {

    @include media(sm) {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 auto;
    }

    &-title {
      margin-bottom: 34px;
      font-size: 12px;
      font-weight: 500;
      letter-spacing: 3px;
      text-transform: uppercase;
    }
    
    &-list {
      display: flex;
      gap: 40px;
    }

    &-link {

      .icon {
        display: block;
        width: 20px;
        height: 20px;
        cursor: pointer;

        &:hover {
          opacity: .7;
        }
      }
    }
  }

  &__apps {

    @include media(sm) {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 auto;
    }
    
    &-list {
      display: flex;
      align-items: center;
      flex-wrap: wrap;
      gap: 10px;

      @include media(sm) {
        flex-wrap: nowrap;
      }
    }

    &-title {
      font-size: 12px;
      margin-bottom: 18px;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 3px;
    }
  }

  &__app {
    max-width: 152px;
  }


  &__submit {
    max-width: 550px;
    width: 100%;

    @include media(sm) {
      display: flex;
      flex-direction: column;
      align-items: center;
      margin: 0 auto;
      width: 100%;
      order: -1;
    }

    &-title {
      font-size: 12px;
      margin-bottom: 20px;
      font-weight: 500;
      text-transform: uppercase;
      letter-spacing: 3px;
    }

    &-form {
      position: relative;
      width: 100%;
    }

    &-input {
      width: 100%;
      border-radius: 50px;
      border: 1px solid #CCCCCC;
      padding: 12px 28px;
      font-size: 18px;
      line-height: 1.3;
      outline: none;
      height: 48px;

      &:focus::placeholder {
        opacity: 0;
        display: none;
      }
    }

    &-btn {
      position: absolute;
      top: 0;
      right: 0;
      bottom: 0;
      display: inline-block;
      width: auto;
      padding: 0 18px 0 59px;
      border: 2px solid var(--color-black);
      border-radius: 50px;
      height: 48px;
      text-transform: uppercase;
      font-size: 15px;
      line-height: 48px;
      cursor: pointer;
    
      span {
        position: relative;
        z-index: 2;
      }

      &:hover {
        opacity: .7;
      }
    
      &::before {
        position: absolute;
        content: '';
        height: 48px;
        width: 48px;
        border-radius: 50px;
        border: 2px solid var(--color-black);
        top: -2px;
        left: -1px;
        bottom: 1px;
        transition: .2s;
      }

      &::after {
        position: absolute;
        content: url('../images/ico-send.svg');
        left: 15px;
        top: 3px;
        opacity: 0;
      }

      @include media(md) {
        padding: 0;
        width: 48px;
        height: 48px;

        &::after {
          opacity: 1;
        }
        &::before {
          opacity: 0;
        }
        span {
          display: none;
        }
      }
    }
  }
}